import { DEV_CURRENT_USER_ID, TESTING_DOMAIN } from "@/app/consts/app-global";
import { AppDispatch, AppGetState } from "@/app/model/store"
import { hookAPI } from "@/app/services/april-hook-api";
import { onlineGeneralAPI } from "@/app/services/april-online-api";
import { BXCompany, BXDeal, BXTask, BXUser } from "@/app/types/bitrix/bitrix-type"
import { departmentActions } from "@/features/Departament";
import { PresentationStateCount } from "./PresSlice";
import { eventTaskActions } from "@/entities/EventTask";
import { API_METHOD } from "@/app/types/api/api-type";
import { setCurrentUser } from "@/features/Departament/model/DepartmentThunk";
import { Portal } from "@/app/types/portal/portal-type";


export const getInitPresentation = (
    // bxCompany: BXCompany, 

) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
        // dispatch(setPreloader(true))
        const state = getState()
        const app = state.app
        const tasks = state.eventTask.tasks as Array<BXTask>
        
        const domain = app.domain || TESTING_DOMAIN //state.app.domain
        const getPortaldata = {
            domain: domain || TESTING_DOMAIN,
            userId: setCurrentUser || DEV_CURRENT_USER_ID,

        }
        for (let index = 0; index < tasks.length; index++) {
            const currentTask = tasks[index];
            const data = {
                currentTask,
                domain
            }
            
            const presTaskData = await hookAPI
                .service(
                    'pres/count',
                    API_METHOD.POST,
                    'presentation',
                    data
                ) as
                {
                    counts: PresentationStateCount
                    deal: BXDeal | null
                } | null
            
            if (presTaskData) {
               
                    dispatch(
                        eventTaskActions.setPresData({
                            taskId: Number(currentTask.id),
                            presData: presTaskData.counts,
                            dealBase: presTaskData.deal
                        })
                    )
                    
                
            }

        }

        const portal = await onlineGeneralAPI
            .service(
                'front/portal',
                API_METHOD.POST,
                'portal',
                getPortaldata
            ) as Portal | null

            
        console.log('portal')
        console.log(portal)

    }

